import React, { useState } from 'react';
import RichTextEditor from 'react-rte-light';

const ChatMessageInput = ({ onSendMessage, placeholder = "Type your message..." }) => {
  const [editorState, setEditorState] = useState(RichTextEditor.createEmptyValue());

  const handleChange = (value) => {
    setEditorState(value);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    // For now, get plain text by removing HTML tags
    const html = editorState.toString('html');
    const text = html.replace(/<[^>]*>/g, '').trim();
    if (text) {
      onSendMessage(text);
      setEditorState(RichTextEditor.createEmptyValue());
    }
  };

  const toolbarConfig = {
    display: ['INLINE_STYLE_BUTTONS', 'BLOCK_TYPE_BUTTONS', 'LINK_BUTTONS'],
    INLINE_STYLE_BUTTONS: [
      { label: 'Bold', style: 'BOLD' },
      { label: 'Italic', style: 'ITALIC' },
      { label: 'Underline', style: 'UNDERLINE' },
      { label: 'Code', style: 'CODE' },
    ],
    BLOCK_TYPE_BUTTONS: [
      { label: 'UL', style: 'unordered-list-item' },
      { label: 'OL', style: 'ordered-list-item' },
      { label: 'Blockquote', style: 'blockquote' },
    ],
  };

  return (
    <form onSubmit={handleSubmit} className="chat-input-form">
      <div className="rich-text-editor-wrapper">
        <RichTextEditor
          value={editorState}
          onChange={handleChange}
          placeholder={placeholder}
          toolbarConfig={toolbarConfig}
          className="chat-rich-editor"
        />
      </div>
      <button type="submit" className="send-button">
        Send
      </button>
    </form>
  );
};

export default ChatMessageInput;